
blockquote {
  display: block;

  padding: 2rem;
  margin: 1.5em 0;
  position: relative;
}
blockquote:before {
  content: '\201C';
  position: absolute;
  top: 0em;
  left: 50%;
  transform: translate(-50%, -50%);

  width: 3rem;
  height: 2rem;
  font: 6em/1.08em 'PT Sans', sans-serif;
  color:white;
  text-align: center;
}
blockquote:after {
  content: "\2013 \2003" attr(cite);
  display: block;
  text-align: right;
  font-size: 0.875em;
  color: grey;
}

/* fix for issue with safari desktop causing images in semantic items to stretch */
.ui.items > .item > .image{
  height: 100%;
}


.ui.form .field .ui.input input, .ui.form .fields .field .ui.input input {
  color: white;
}


.ui.form textarea {
  color: white !important;
  background: rgba(0,0,0,0) !important;
  border: 1px solid white !important;
}